<template>
  <!-- <div class="wrapper"> -->
  <!-- <div class="index"> -->
  <div class="news">
    <div class="container-com">
      <div class="product-in-catalog">
        <h1>{{ product.name }}</h1>
        <div class="product-in-catalog__info">
          <div class="product-in-catalog__img">
            <img
              :src="
                require('../assets/img/product/' + product.article + '.png')
              "
              alt="img"
              width="166"
              height="166"
            />
          </div>
          <div class="product-in-catalog__text text">
            <!-- <p class="text__product">{</p> -->
            <div class="product-in-catalog__block">
              <p class="text__product2">Артикль:</p>
              <p class="text__description">{{ product.article }}</p>
              <p class="text__product2">Описание товара:</p>
              <p class="text__description">{{ product.description }}</p>
              <p class="text__product2">Рекомендации:</p>
              <p class="text__description">{{ product.recom }}</p>
              <div class="product-in-catalog__wrap">
                <div class="product-in-catalog__wrap1">
                  <p class="text__product2">Цена:</p>
                  <p class="text__description">{{ product.price }} Рублей</p>
                </div>
                <div class="product-in-catalog__wrap2">
                  <p class="text__product2">Объем:</p>
                  <p class="text__description">{{ product.volume }}</p>
                </div>
              </div>
            </div>
            <!-- <div class="price__block">
                                    <span>Цена:</span>
                                    <p>{{product.price}} рублей</p>
                                </div> -->
            <div class="text__block2"></div>
            <div class="product-in-catalog__b">
              <div class="product-in-catalog__btn btn3">
                <a href="#popup2" class="popup-link">
                  <input
                    class="btn3__span"
                    type="button"
                    value="Заказать"
                    @mousedown="nameproductfun(product)"
                /></a>
              </div>
              <div class="product-in-catalog__btn btn2">
                <router-link :to="`/categories/${path.path}`"
                  ><span class="btn2__span">К списку товаров</span></router-link
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="popup2" class="popup2">
      <!-- <a href="#header" class="popup2__area"></a> -->
      <div class="popup2__body">
        <div class="popup2__content">
          <div class="popup2__container">
            <div class="popup2__title">
              <a href="#" class="popup2__close close-popup">x</a>
            </div>
            <div class="popup2__text">
              <p class="popup2__text-p">Заявка для заказа!</p>
              <form action="" class="contact__form block" method="POST">
                <div class="block_info">
                  <p>Ваши Имя и Фамилия</p>
                  <input
                    type="text"
                    id="name"
                    v-model="name"
                    @blur="$v.name.$touch()"
                    placeholder="Иваи Иванов"
                  />
                  <span v-if="$v.name.$error" class="span-error">
                    <template v-if="!$v.name.maxLength">
                      Длина имени не должна превышать
                      {{ $v.name.$params.maxLength.max }}
                      символов
                    </template>
                    <template v-else-if="!$v.name.alpha">
                      Введите Имя и Фамилию
                    </template>
                  </span>
                </div>
                <div class="block_info">
                  <p>Ваш номер телефона</p>
                  <input
                    type="text"
                    id="phone"
                    v-model="phone"
                    v-mask="'+7(###)###-####'"
                    placeholder="+7(###)###-####"
                    @blur="$v.phone.$touch()"
                  />
                  <span v-if="$v.phone.$error" class="span-error">
                    <template v-if="!$v.phone.minLength">
                      Номер введен некорректно!
                    </template>
                  </span>
                </div>
                <div class="block_info">
                  <p>Электронная почта</p>
                  <input
                    type="text"
                    id="mail"
                    v-model="mail"
                    @blur="$v.mail.$touch()"
                    placeholder="example@email.com"
                  />
                  <span v-if="$v.mail.$error" class="span-error">
                    <template v-if="!$v.mail.validFormat">
                      Укажите корректный адрес электронной почты.
                    </template>
                  </span>
                </div>
                <span v-if="$v.$invalid" class="span-button">
                  <template>
                    <p>Перед отправкой данный заполните все поля!</p>
                  </template>
                </span>
                <div class="block__btn btn2" type="submit">
                  <a href="#popup" @mousedown="index">
                    <button
                      type="submit"
                      value="Отправить"
                      class="btn2__span"
                      :disabled="$v.$invalid"
                      @click="postPost(name, mail, phone, productname)"
                    >
                      Отправить
                    </button></a
                  >
                  <!-- <a href="#popup" @mousedown="index"> <input type="button" value="Отправить"
                                                class="btn2__span"
                                                @mousedown="postPost(name,mail,phone,productname)"></a> -->
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- </div> -->
  <!-- </div> -->
</template>
<script>
import TopMenu from "@/components/top-menu.vue";
import LeftMenu from "@/components/left-menu.vue";
import Aside from "@/components/aside.vue";
import Footer from "@/components/footer.vue";
import popups from "@/popup/popup.js";
import axios from "axios";
import {
  required,
  maxLength,
  minLength,
  email,
} from "vuelidate/lib/validators";
export default {
  name: "cosm-product",
  data() {
    return {
      name: null,
      mail: null,
      phone: null,
      productname: [],
    };
  },
  validations: {
    mail: {
      validFormat: (val) =>
        /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/.test(
          val
        ),
    },
    name: {
      required,
      maxLength: maxLength(30),
      alpha: (val) => /^[а-яё]+(?: |, |-)+[а-яё]*$/i.test(val),
    },
    phone: {
      minLength: minLength(15),
    },
  },
  computed: {
    product: {
      get: function () {
        return this.$store.getters.ProductById(this.$route.params.article);
      },
    },
    path() {
      return this.$store.getters.findByCateg(this.$route.params.path);
    },
  },
  methods: {
    postPost(name, mail, phone, productname) {
      try {
        let data = axios.post("http://31.31.203.119/api/order", {
          name: name,
          mail: mail,
          phone: phone,
          productname: productname[0].name,
        });
        if (data.message == "success") {
        } else {
          throw new Error(data.status);
        }
      } catch (e) {
        this.error = e;
        console.log(e);
      }
    },
    nameproductfun(product) {
      this.productname.push({
        name: product.name,
      });
    },
    index() {
      this.$router.push("/");
    },
  },
  components: {
    TopMenu,
    LeftMenu,
    Aside,
    Footer,
    popups,
  },
  mounted() {
    const popupLinks = document.querySelectorAll(".popup-link");
    const body = document.querySelector("body");
    const lockPadding = document.querySelectorAll(".lock-padding");
    let unlock = true;

    const timeout = 800;

    if (popupLinks.length > 0) {
      for (let index = 0; index < popupLinks.length; index++) {
        const popupLink = popupLinks[index];
        popupLink.addEventListener("click", function (e) {
          const popupName = popupLink.getAttribute("href").replace("#", "");
          const curentPopup = document.getElementById(popupName);
          popupOpen(curentPopup);
          e.preventDefault();
        });
      }
    }
    const popupCloseIcon = document.querySelectorAll(".close-popup");
    if (popupCloseIcon.length > 0) {
      for (let index = 0; index < popupCloseIcon.length; index++) {
        const el = popupCloseIcon[index];
        el.addEventListener("click", function (e) {
          popupClose(el.closest(".popup2"));
          e.preventDefault();
        });
      }
    }

    function popupOpen(curentPopup) {
      if (curentPopup && unlock) {
        const popupActive = document.querySelector(".popup2.open");
        if (popupActive) {
          popupClose(popupActive, false);
        } else {
          bodyLock();
        }
        curentPopup.classList.add("open");
        curentPopup.addEventListener("click", function (e) {
          if (!e.target.closest(".popup2__content")) {
            popupClose(e.target.closest(".popup2"));
          }
        });
      }
    }

    function popupClose(popupActive, doUnlock = true) {
      if (unlock) {
        popupActive.classList.remove("open");
        if (doUnlock) {
          bodyUnLock();
        }
      }
    }

    function bodyLock() {
      const lockPaddingValue =
        window.innerWidth -
        document.querySelector(".wrapper").offsetWidth +
        "px";

      if (lockPadding.length > 0) {
        for (let index = 0; index < lockPadding.length; index++) {
          const el = lockPadding[index];
          el.style.paddingRight = lockPaddingValue;
        }
      }
      body.style.paddingRight = lockPaddingValue;
      body.classList.add("lock");

      unlock = false;
      setTimeout(function () {
        unlock = true;
      }, timeout);
    }

    function bodyUnLock() {
      setTimeout(function () {
        if (lockPadding.length > 0) {
          for (let index = 0; index < lockPadding.length; index++) {
            const el = lockPadding[index];
            el.style.paddingRight = "0px";
          }
        }
        body.style.paddingRight = "0px";
        body.classList.remove("lock");
      }, timeout);

      unlock = false;
      setTimeout(function () {
        unlock = true;
      }, timeout);
    }

    document.addEventListener("keydown", function (e) {
      if (e.which === 27) {
        const popupActive = document.querySelector(".popup.open");
        popupClose(popupActive);
      }
    });

    (function () {
      // проверяем поддержку
      if (!Element.prototype.closest) {
        // реализуем
        Element.prototype.closest = function (css) {
          var node = this;
          while (node) {
            if (node.matches(css)) return node;
            else node = node.parentElement;
          }
          return null;
        };
      }
    })();
    (function () {
      // проверяем поддержку
      if (!Element.prototype.matches) {
        // определяем свойство
        Element.prototype.matches =
          Element.prototype.matchesSelector ||
          Element.prototype.webkitMatchesSelector ||
          Element.prototype.mozMatchesSelector ||
          Element.prototype.msMatchesSelector;
      }
    })();

    // export default popup;
  },
};
</script>